import React, { useState } from 'react';
import axios from 'axios';
import styled from 'styled-components'; // Styled Components  
import Button from 'components/common/Button';


// Styled Components    
const Container = styled.div`
  width: ${(props) => props.width || '100%'};
  height: 230px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
`;

const InputWrapper = styled.div`
  width: 100%;
  height: 40px;
  margin-bottom : 10px;
`;

const TextareaWrapper = styled.div`
  width: 100%;
  height: 140px;
`;

const Input = styled.input`
  width: 100%;
  height: 100%;
  border: 1px solid #ccc;
  padding: 10px;
`;

const Textarea = styled.textarea`
  width: 100%;
  height: 100%;
  border: 1px solid #ccc;
  padding: 10px;
`;

const StyledButton = styled.div`
  position: relative;
  bottom: 0px;
  right: -300px;
  
`;

function ReviewWrite({ width, height, handleCloseClick,restaurantName }) {
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');

  const handleTitleChange = (e) => {
    setTitle(e.target.value);
  };

  const handleContentChange = (e) => {
    setContent(e.target.value);
  };

  const sendDataToServer = () => {
    axios
      .post(`http://223.130.131.136:8080/review/${restaurantName}/post`, { rating: title, content: content })
      .then((response) => {
        console.log(response.data);
      })
      .catch((error) => {
        console.error(' : ', error);
      });
  };

  return (
    <Container width={width} height={height}>
      <InputWrapper>
        <Input
          type="text"
          value={title}
          onChange={handleTitleChange}
          placeholder=" "
        />
      </InputWrapper>

      <TextareaWrapper>
        <Textarea
          value={content}
          onChange={handleContentChange}
          placeholder=" "
        />
      </TextareaWrapper>
      <StyledButton>
        <Button width='100%' height='30px' onClick={sendDataToServer}></Button>
      </StyledButton>
      <Button width='100%' height='30px' onClick={handleCloseClick}></Button>
      
    </Container>
  );
}

export default ReviewWrite;