import React, { useState, useEffect } from "react";
import StoreBoardItem from "components/StoreBoard/StoreBoardItem";
import styled from "styled-components";
import axios from "axios";
import Button from "components/common/Button";
import ReviewWrite from "./ReviewWrite";

import { Call } from "@mui/icons-material";
import LocationOnIcon from '@mui/icons-material/LocationOn';
import StarIcon from '@mui/icons-material/Star';

const StoreInfo = ({ title }) => {
  const [data, setData] = useState([]); //   
  const fetchAllData = async () => {
    try {
      const response = await axios.get(`http://223.130.131.136:8080/boards/${title}?`);
      const allData = response.data.posts;
      setData(allData);
    } catch (error) {
      console.error("    :", error);
    }
  };
  
  const [infoData, setInfoData] = useState([]);
  const fetchInfoData = async () => {
    try {
      const response = await axios.get(`http://223.130.131.136:8080/boards/restaurantInfo/${title}`)
      const allData = response.data;
      setInfoData(allData);
    } catch (error) {
      console.error("    :", error);
    }
  }

  useEffect(() => {
    fetchAllData();
    fetchInfoData();
  }, [title]);

  //  : isWriting   
  const [isWriting, setIsWriting] = useState(false);

  const handleWriteClick = () => setIsWriting(true);
  const handleCloseClick = () => setIsWriting(false);
  
  return (
    <>
    <Styledh1>{title}</Styledh1>
    <StoreBoardItem width='400px' height='200px'>
      <StarIcon />
      <LocationOnIcon />
      {/*  */}
      <p>{infoData.address}</p>
      <Call />
      {/*  */}
      <p>{infoData.phone}</p>
      {/*  */}
      </StoreBoardItem>

      <StyledDiv>
        <h2>Review</h2>
        <ButtonWrapper> 
          {/*    handleWriteClick  */}
          <Button width='70px' height='30px' onClick={handleWriteClick}></Button>
        </ButtonWrapper>

        {isWriting ? (
          // isWriting true  ReviewWrite  
          <ReviewWrite height='200px' handleCloseClick={handleCloseClick} restaurantName={title}/> 
        ) : (
          // isWriting false    
          <div style={{ height: "230px", overflowY: "auto" }}>
            {data.map((restaurant, index) => (
              <div key={index} onClick={() => window.open(`http://223.130.131.136:8080/boards/${restaurant.posts}?`)}>
                <StoreBoardItem height={'80px'}>
                <p><StyledP>{restaurant.content}</StyledP></p>
                {/* href={`http://223.130.131.136:8080/boards/${restaurant.posts}?`} */}
                </StoreBoardItem>
              </div>
              // storeBoardItem onclik  , div key  onclick 
            ))}
          </div>  
        )}
      </StyledDiv>
    </>
  )
}

const Styledh1 = styled.h1`
  position: relative;
  top: -20px;
`

const StyledDiv = styled.div`
  position: relative;
  top: -10px;
`

const ButtonWrapper = styled.div`
  /*     */
  position: absolute;
  top: 0;
  right: 0;
`
const StyledP = styled.a`
  background-color : blue;
  font-size : 30px;
  text-align : center;
`
export default StoreInfo;
