import axios from 'axios';
import { useEffect, useState } from 'react';
import Pagination from 'react-js-pagination';
import styled from 'styled-components';
import StoreBoardItem from './StoreBoardItem';
import Button from 'components/common/Button';

const Paging = ({ restaurant }) => { // restaurant  props 
  const [data, setData] = useState([]);
  const [page, setPage] = useState(1);
  const items = 4;

  useEffect(() => {
    const getClick = async () => {
      try {
        const response = await axios.get(`http://223.130.131.136:8080/boards/${restaurant}?`);
        setData(response.data);
      } catch (error) {
        console.error('    :', error);
      }
    };
    getClick();
  }, [restaurant]); // restaurant      

  const handlePageChange = (page) => {
    setPage(page);
  };

  if (!data.posts) {
    return null; 
  }

  return (
    <StyledDiv>
      <MainBoardTitle>{restaurant}</MainBoardTitle>
      <ButtonWrapper>
        <Button width='70px' height='30px' onClick={() => { window.location.href = 'http://223.130.131.136:8080/boards/post'}}></Button>
      </ButtonWrapper>
      {data.posts
        .slice(items * (page - 1), items * page)
        .map((post, i) => {
          return (
            <div key={i}>
              <StoreBoardItem height='90px'>
                <a href={`http://223.130.131.136:8080/boards/post/${post.postId}/detail`}>{post.content}</a>
              </StoreBoardItem>
            </div>
          );
        })}
      <PaginationBox>
        <Pagination
          activePage={page}
          itemsCountPerPage={items}
          totalItemsCount={data.posts.length}
          pageRangeDisplayed={5}
          onChange={handlePageChange}
        />
      </PaginationBox>
    </StyledDiv>
  );
};

const ButtonWrapper = styled.div`
  position: absolute;
  top: -5px; /*  (y)   */
  right: 0; /*  (x)   */
`;

const PaginationBox = styled.div`
  .pagination {
    display: flex;
    justify-content: center;
    margin-top: 15px;
  }
  ul {
    list-style: none;
    padding: 0;
  }
  ul.pagination li {
    display: inline-block;
    width: 30px;
    height: 30px;
    border: 1px solid #e2e2e2;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
  }
  ul.pagination li:first-child {
    border-radius: 5px 0 0 5px;
  }
  ul.pagination li:last-child {
    border-radius: 0 5px 5px 0;
  }
  ul.pagination li a {
    text-decoration: none;
    color: #ff9704;
    font-size: 1rem;
  }
  ul.pagination li.active a {
    color: white;
  }
  ul.pagination li.active {
    background-color: #ff9704;
  }
  ul.pagination li a:hover,
  ul.pagination li a.active {
    color: ff9704;
  }
`;

const StyledDiv = styled.div`
  position: relative;
  top: -30px;
`;
const MainBoardTitle = styled.h3`
  position: relative;
  top: -5px;
`;

export default Paging;